	/* image container*/

.image_container {
  display: block;
  position:relative;
  width: 100%;
  margin-bottom: 20px;
  padding: 5px;
}
.image_container img {
  width:100%
}
.overlay {
  position: absolute; 
  bottom: 20px;/*because of padding*/
  left: 10px;
  padding: 5px;
  z-index: 2;
  background: #000000;
  transition: .3s ease;
  opacity:0;
  font: 18px "Montserrat", sans-serif;
  color: #ccbdb3;
}

.image_container:hover .overlay {
  opacity: 1;
}

.image_container:hover .image_container img {
  filter: grayscale(100%);
}

	/*----------------modal gallery----------------*/


.modal_image {  /*<-- image is thumbnail*/
  cursor: pointer;
  transition: 0.3s;
  width: 100%;
  margin-bottom: 20px;
}

.modal_image:hover {opacity: 0.7;}

	/* background */

.modal {
  display: none;
  position: fixed; 
  z-index: 3;
  padding: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #000000; /* Fallback color */
  background-color: rgba(0,0,0,0.9);
  vertical-align: middle;
}

	/* Modal Content (image) */

.modal_content {
  margin: auto;
  /*height:100%;*/
  max-height:100%;
  max-width:100%;
  display: block;
}
	/* Caption of Modal Image */

#Caption { 
  display: block;
  position: fixed;
  bottom: 15vh;
  left: 10vw;
  background-color: #000000;
  padding: 10px;
  text-align: center;
  font: 400 22px/1 "Montserrat", sans-serif;
  color: #ffffff;
  z-index:3000;
}

/* Add Animation */
.modal_content, #caption{  
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
} 

@-webkit-keyframes zoom {
  from {-webkit-transform:scale(0)} 
  to {-webkit-transform:scale(1)}
}

@keyframes zoom {
  from {transform:scale(0)} 
  to {transform:scale(1)}
}

	/* The Close Button */

.close {
  position: fixed;
  top: 10vh;
  right: 5vw;
  color: #ffffff;
  font-size: 60px;
  font-weight: bold;
  transition: 0.3s;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}

.close:hover,
.close:focus {
  color: red;
  text-decoration: none;
  cursor: pointer;
}

	/* other button */

/* Next & previous buttons */

.prev,
.next {
  cursor: pointer;
  position: fixed;
  right: 5vw;
  width: auto;
  padding: 5px;
  color: white;
  font-weight: bold;
  font-size: 30px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
  -webkit-user-select: none;
}
.info {/*hidden
  display: none;*/
  cursor: pointer;
  position: fixed;
  bottom: 15vh;
  left: 2vw;
  width: auto;
  background-color: #ffffff;
  color: #000000;
  font-weight: bold;
  font-size: 30px;
  transition: 0.6s ease;
  user-select: none;
  -webkit-user-select: none;
}

/* Position the "button" */
.next {
  top: 60vh;
}
.prev {
  bottom: 60vh;
} 


/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover,
.info:hover{
  color: #737373;
}